<template>
  <div v-theme:column="'narrow'" id="show-blogs">
    <!-- 因为v-theme="'wide'" 用“”是字符串所以wide要用单引号-->
    <!-- v-rainbow与v-theme为自己定义的一个指令 -->
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索" />
    <div v-for="(blog,index) in fifleblog" :key="index" class="single-blog" @click="toGo(blog.id)">
      <!-- <router-link :to="'/blog/' + blog.id"> -->
      <h2 v-rainbow>{{ blog.title | to-uppercase}}</h2>
      <!-- to-uppercase -->
      <!-- </router-link> -->
      <article>{{ blog.content | snippet}}</article>
    </div>
  </div>
</template>

<script>
import blog from "../../static/posts.json";
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  methods: {
    toGo(val) {
      this.$router.push({
        name: "SingleBlog",
        params: {
          id: val,
          name: "1231321"
        }
      });
    }
  },
  created() {
    this.$axios
      .get("https://vuedemo-e7ade.firebaseio.com/posts.json")
      .then(res => {
        console.log(res)
        for (let i in res.data) {
          res.data[i].id=i
          this.blogs.push(res.data[i]);
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  computed: {
    fifleblog() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search); //拿到用户所输入的内容数据匹配title
      });
    }
  },
  filters: {
    toUppercase(vaule) {
      return vaule.toUpperCase();
    }
  },
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8); //随机一个颜色
      }
    }
  }
};
</script>


<style>
h1 {
  text-align: center;
}
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box; /* box-sizing 属性允许您以特定的方式定义匹配某个区域的特定元素,需要并排放置两个带边框的框，可通过将 box-sizing 设置为 "border-box"。这可令浏览器呈现出带有指定宽度和高度的框，并把边框和内边距放入框中。 */
  background: #eee;
  border: 1px dotted #aaa;
}
#show-blogs a {
  color: #444;
  text-decoration: none;
}
input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



