<template>
  <div id="add-blog">
    <!-- <BlogHeader></BlogHeader> -->
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题：</label>
      <input type="text" v-model="blog.title" required />
      <!-- required 属性规定必需在提交之前填写输入字段。 -->

      <label>博客内容：</label>
      <textarea v-model="blog.content"></textarea>
      <!-- <textarea> 标签定义多行的文本输入控件。 -->

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />

        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories" />

        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories" />

        <label>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories" />
      </div>
    </form>
    <label>作者：</label>
    <select v-model="blog.author">
      <option v-for="(author,index) in authors" :key="index">{{ author }}</option>
    </select>
    <!-- select 元素可创建单选或多选菜单 -->
    <button @click.prevent="post">添加博客</button>
    
    <div v-if="submmited">
      <h3>您的博客已经发送成功!</h3>
    </div>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{ blog.title }}</p>
      <p>博客内容：</p>
      <p>{{ blog.content }}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="(category,index) in blog.categories" :key="index">{{ category }}</li>
      </ul>
      <p>作者:{{ blog.author }}</p>
    </div>
  </div>
</template>

<script>
import BlogHeader from '../components/BlogHeader'
export default {
  name: "add-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },
      authors: ["Hemiah", "Henry", "Bucky"],
      submmited: false //submmited是否已经点击提交了事件
    };
  },
  // components:{
  //   BlogHeader
  // },
  methods: {
    // post() {
    //   this.$http
    //     .post("http://jsonplaceholder.typicode.com/posts", {
    //       title: this.blog.title,
    //       body: this.blog.content,
    //       userId: 1
    //     })
    //     .then(function(data) {
    //       console.log(data);
    //       this.submmited = true;
    //     });
    // }
    post() {
      this.$axios
        .post("https://vuedemo-e7ade.firebaseio.com/posts.json",{
          title: this.blog.title,
          content: this.blog.content,
        })
        .then(res => {
          console.log(res);
          this.submmited = true;
        })
        .catch(err => {
          console.log(err);
        });
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing:border-box;
}
#add-blog{
  margin:20px auto;
  max-width: 600px;/* 最大宽度 */
  padding: 20px;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}/* 博客内容 */
textarea{
  height: 250px;
}
#checkboxes label{
  display: inline-block;/* 不让分行，摆在一行 */
  margin-top:0;/* 紧贴上面内容 */
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;/* 摆在右边 */
}
button{
  display: block;
  margin: 20px 0;
  background: crimson;
  color:#fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;/* 设置圆角 */
  font-size: 18px;
  cursor: pointer;/* 光标变成一只手 */
}
#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>



