<template>
  <div id="single-blog">
    <h1>{{ blog.title }}</h1>
    <article>{{ blog.content }}</article>
    <p>{{ blog.author }}</p>
    <p>分类:</p>
    <ul>
      <li v-for="(category,index) in blog.category" :key="index">
          {{ category }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {} //显示id的博客
    };
  },
  created() {
    console.log(this.$route.params)
    this.$axios
      .get("https://vuedemo-e7ade.firebaseio.com/posts/" + this.id + ".json")
      .then(res => {
        console.log(res)
        this.blog = res.data;
      })
      .catch(err=>{
        console.log(err)
      })
  }
};
</script>

<style>
#single-blog {
  max-width: 960;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}
</style>


